"use strict";

const Car = require("src/pages/car/car.js");
const Road = require("src/pages/car/road.js");
const Visualizer = require("src/pages/car/visualizer.js");
const NeuralNetwork = require("src/pages/car/network.js");


document.addEventListener('DOMContentLoaded', function () {


    document.getElementById("save").addEventListener("click", save);
    document.getElementById("discard").addEventListener("click", discard);

    const carCanvas = document.querySelector("#car-canvas");
    const networkCanvas = document.querySelector("#network-canvas");

    const carCtx = carCanvas.getContext("2d");
    const networkCtx = networkCanvas.getContext("2d");
    const road = new Road(carCanvas.width / 2, carCanvas.width * 0.9);
    // const car = new Car(road.getLaneCenter(1),100,30,50,"KEYS");
    // const car = new Car(road.getLaneCenter(1),100,30,50,"AI");

    const cars = generateCars(1);
    let bestCar=cars[0];
    if(localStorage.getItem("bestBrain")){
        for(let i=0;i<cars.length;i++){
            cars[i].brain=JSON.parse(
                localStorage.getItem("bestBrain"));
            if(i!=0){
                NeuralNetwork.mutate(cars[i].brain,0.1);
            }
        }
    }


    function save(){
        localStorage.setItem("bestBrain",
            JSON.stringify(bestCar.brain));
    }

    function discard(){
        localStorage.removeItem("bestBrain");
    }

    const traffic=[
        new Car(road.getLaneCenter(1),-100,30,50,"DUMMY",2),
        new Car(road.getLaneCenter(0),-300,30,50,"DUMMY",2),
        new Car(road.getLaneCenter(2),-300,30,50,"DUMMY",2),
        new Car(road.getLaneCenter(0),-500,30,50,"DUMMY",2),
        new Car(road.getLaneCenter(1),-500,30,50,"DUMMY",2),
        new Car(road.getLaneCenter(1),-700,30,50,"DUMMY",2),
        new Car(road.getLaneCenter(2),-700,30,50,"DUMMY",2),
    ];


    function generateCars(N){
        const cars=[];
        for(let i=1;i<=N;i++){
            cars.push(new Car(road.getLaneCenter(1),100,30,50,"AI"));
        }
        return cars;
    }

    animate();

    function animate (time) {
        for(let i=0;i<traffic.length;i++){
            traffic[i].update(road.borders,[]);
        }

        for(let i=0;i<cars.length;i++){
            cars[i].update(road.borders, traffic);
        }
        bestCar=cars.find(
            c=>c.y==Math.min(
                ...cars.map(c=>c.y)
            ));


        carCanvas.height = window.innerHeight;
        networkCanvas.height = window.innerHeight;


        carCtx.save();
        carCtx.translate(0, -bestCar.y + carCanvas.height * 0.75)

        road.draw(carCtx);
        for(let i=0;i<traffic.length;i++){
            traffic[i].draw(carCtx,"#7bb24d");
        }

        carCtx.globalAlpha = 0.2;
        for(let i=0;i<cars.length;i++){
            cars[i].draw(carCtx, "#de6717");
        }
        carCtx.globalAlpha = 1;
        bestCar.draw(carCtx, "#de6717", true);

        carCtx.restore();

        networkCtx.lineDashOffset = -time / 50;
        Visualizer.drawNetwork(networkCtx, bestCar.brain);
        requestAnimationFrame(animate);
    }
});